#top-nav{background:lightgray;display:flex;top:0;z-index:1;background:white;color:#757575}@media (max-width: 800px){#top-nav{font-size:0.8em}}@media (max-width: 500px){#top-nav{font-size:0.7em}}#top-nav .pages{display:flex;align-items:center;position:relative;margin:16px;margin-bottom:10px}#top-nav .pages>div{padding:0 30px;-webkit-transition:all 0.5s;transition:all 0.5s;text-align:center}@media (max-width: 800px){#top-nav .pages>div{padding:0 10px}}@media (max-width: 500px){#top-nav .pages>div{padding:0 5px}}@media (max-width: 450px){#top-nav .pages>div{font-size:12px}}#top-nav .pages>div:hover{color:black}#top-nav .pages>div a{color:unset;text-decoration:none}#top-nav .pages>div.active{color:black;font-weight:bold}#top-nav .pages #highlighter{position:absolute;bottom:0;width:0px;height:3px;background:#ff8400;-webkit-transition:left 0.9s cubic-bezier(0.68, -0.55, 0.27, 1.55),width 0.5s ease-in-out,opacity 0.5s;transition:left 0.9s cubic-bezier(0.68, -0.55, 0.27, 1.55),width 0.5s ease-in-out,opacity 0.5s;padding:0}@media (max-width: 800px){#top-nav .pages #highlighter{bottom:-2px}}#top-nav .title{font-size:32px;margin-right:50px;font-family:'Barlow', sans-serif;margin-top:-5px;padding:15px;color:black}@media (max-width: 800px){#top-nav .title{font-size:20px;margin-right:15px}}@media (max-width: 500px){#top-nav .title{font-size:16px;margin-right:0;flex-grow:1}}

