.tech-list{display:flex;flex-wrap:wrap;font-size:0.8em}.tech-list .tech{margin-right:10px;background:#cecece;border-radius:100px;padding:2px 12px}.tech-list .tech.front-end{background:#ecc0ff}.tech-list .tech.back-end{background:#c0ceff}.tech-list .tech.database{background:#ffc0c0}

.expansion-card{display:flex;flex-direction:column;-webkit-transition:all 0.5s;transition:all 0.5s}.expansion-card .contents{display:flex;flex-direction:column}.expansion-card .top{display:flex;flex-direction:column}.expansion-card .top-bar{display:flex;justify-content:space-between}.expansion-card .top-bar:hover .expander svg{color:#000}.expansion-card .top-bar .expander{display:flex;justify-content:flex-end}.expansion-card .top-bar .expander svg{display:none;color:#9c9c9c;fill:currentColor;margin-top:-10px;-webkit-transition:all 0.3s;transition:all 0.3s;width:40px;height:40px}.expansion-card .top-bar .expander.expanded svg{color:#000;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.expansion-card .top-bar.has-content{cursor:pointer}.expansion-card .top-bar.has-content svg{display:unset}.expansion-card .title{font-size:0.9em;margin-right:50px}.expansion-card .title div{display:flex}.expansion-card .details{font-size:0.8em;padding:0;padding-top:15px;padding-right:60px;padding-left:60px;margin:0;overflow:hidden;-webkit-transition:all 0.4s ease-in-out;transition:all 0.4s ease-in-out}@media (max-width: 800px){.expansion-card .details{padding-right:40px;padding-left:40px}}@media (max-width: 500px){.expansion-card .details{padding-right:0px;padding-left:0px}}.expansion-card .details.expanded{margin-bottom:20px}@media (max-width: 500px){.expansion-card .details.expanded{margin-bottom:0}}.expansion-card .details.hidden{visibility:hidden;position:fixed;max-width:900px}

.photo-gallery .thumb{background:white;cursor:pointer;position:relative;display:inline-flex;-webkit-transition:all .3s;transition:all .3s;padding:5px;margin:5px;box-shadow:1px 1px 7px 2px #00000036}.photo-gallery .thumb .img{width:100px;height:100px;background-repeat:no-repeat;background-size:contain;background-position:center}@media (max-width: 600px){.photo-gallery .thumb .img{width:75px;height:75px}}.photo-gallery .thumb .icon{position:absolute;bottom:0;right:0;display:flex;justify-content:center;align-items:center;background:#ffffffa8;padding:4px}.photo-gallery .thumb svg{color:#4c4c4c;fill:currentColor;-webkit-transition:all .3s;transition:all .3s}.photo-gallery .thumb:hover{box-shadow:1px 1px 7px 2px #0000006b}.photo-gallery .thumb:hover svg{color:black}.photo-viewer-backdrop{position:fixed;top:0;height:0;left:0;right:0;opacity:0;-webkit-transition:opacity 0.3s;transition:opacity 0.3s;background:#00000078;z-index:-1;display:flex;justify-content:center;align-items:center;flex-direction:column}.photo-viewer-backdrop.visible{height:auto;opacity:1;bottom:0;z-index:1}.photo-viewer-backdrop.full-height{height:auto;bottom:0;z-index:1}.photo-viewer-backdrop .current-photo{width:calc(100% - 75px * 2);height:calc(100% - 30px * 2);background:white;position:absolute;display:flex;z-index:1;margin:auto;top:30px}@media (max-width: 800px){.photo-viewer-backdrop .current-photo{width:calc(100% - 30px * 2);height:calc(100% - 30px * 2)}}.photo-viewer-backdrop .mid{display:flex;flex-direction:column;height:100%}.photo-viewer-backdrop .caption{background:white;font-size:0.9em;padding:20px 0}@media (max-width: 800px){.photo-viewer-backdrop .caption{padding:10px 0}}@media (max-width: 500px){.photo-viewer-backdrop .caption{padding:0}}.photo-viewer-backdrop .mid{display:flex;justify-content:center;position:relative;flex-grow:1}.photo-viewer-backdrop .count{color:gray;position:absolute;top:0;right:0;padding:10px 0;width:75px;text-align:center}@media (max-width: 800px){.photo-viewer-backdrop .count{width:50px}}.photo-viewer-backdrop .img-container{flex-grow:1;background:#c7c7c799;padding:10px;display:flex;box-shadow:1px 1px 7px 2px #00000036;align-items:center;margin:0 10px;margin-bottom:8px;margin-top:40px}.photo-viewer-backdrop .img{box-sizing:border-box;background-repeat:no-repeat;background-size:contain;background-position:center;width:100%;height:100%}.photo-viewer-backdrop .nav{display:flex;align-items:center;color:#c7c7c7}.photo-viewer-backdrop .nav:hover{color:gray;color:#ff8400}.photo-viewer-backdrop .nav svg{visibility:hidden;padding:0;height:75px;width:75px;fill:currentColor}@media (max-width: 800px){.photo-viewer-backdrop .nav svg{height:50px;width:50px}}@media (max-width: 500px){.photo-viewer-backdrop .nav svg{height:20px;width:20px}}.photo-viewer-backdrop .nav.show-nav{cursor:pointer}.photo-viewer-backdrop .nav.show-nav svg{visibility:visible}

